.title-line {
  max-width: 100%;
  width: var(--bl-width);
  height: calc(var(--bl-heigth)  + var(--bl-row-gap)  + 1px);
  position: relative;
}

.title-line__1 {
  width: var(--bl-break-point);
  left: calc(-1 * var(--cnt-padding) );
  top: var(--bl-heigth);
  height: 1px;
  z-index: 1;
  background-color: #7be34a;
  position: absolute;
  box-shadow: 0 4px 25px rgba(123, 227, 74, .25);
}

.title-line__2 {
  width: 1px;
  height: var(--bl-heigth);
  left: calc(var(--bl-break-point)  - var(--cnt-padding) );
  z-index: 1;
  background-color: #7be34a;
  position: absolute;
  top: 1px;
  box-shadow: 0 4px 25px rgba(123, 227, 74, .25);
}

.title-line__3 {
  left: calc(var(--bl-break-point)  - var(--cnt-padding) );
  width: calc(var(--bl-width)  - var(--bl-break-point) );
  z-index: 1;
  height: 1px;
  background-color: #7be34a;
  position: absolute;
  top: 0;
  box-shadow: 0 4px 25px rgba(123, 227, 74, .25);
}

