    width: calc(var(--bl-width)  - var(--bl-break-point)  + var(--cnt-padding) * 2);
  }
}

.title-line__4 {
  width: calc(var(--bl-break-point)  + var(--bl-row-gap) );
  left: calc(-1 * var(--cnt-padding) );
  top: calc(var(--bl-heigth)  + var(--bl-row-gap) );
  height: 1px;
  z-index: 1;
  background-color: #28780d;
  position: absolute;
  box-shadow: 0 4px 25px rgba(40, 120, 13, .25);
}

.title-line__5 {
  width: 1px;
  height: var(--bl-heigth);
  left: calc(var(--bl-break-point)  + var(--bl-row-gap)  - var(--cnt-padding) );
  top: calc(1px + var(--bl-row-gap) );
